<template>
  <div class="color-picker">
    <input type="color" v-model="selectedColor" @change="updateColor" />
    <div class="color-preview" :style="{ backgroundColor: selectedColor }"></div>
  </div>
</template>

<script>
export default {
  props: {
    defaultValue: {
      type: String,
      default: '#ffffff',
    },
  },
  data() {
    return {
      selectedColor: this.defaultValue,
    };
  },
  methods: {
    updateColor(event) {
      this.selectedColor = event.target.value;
    },
  },
};
</script>

<style scoped>
.color-picker {
  display: flex;
  align-items: center;
}

.color-preview {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-left: 10px;
}
</style>